module.exports = "import * as React from 'react';\r\nimport { Pagination } from '../../../../components/';\r\n\r\nexport default class App extends React.Component {\r\n  state = {\r\n    current: 1,\r\n  }\r\n\r\n  render() {\r\n    return (\r\n      <Pagination\r\n        current={this.state.current}\r\n        total={200}\r\n        onChange={(current) => {\r\n          this.setState({current});\r\n        }}\r\n        next=\"下一页\"\r\n        previous=\"上一页\"\r\n      />\r\n    )\r\n  }\r\n}\r\n"