module.exports = "import * as React from 'react';\r\nimport { Slider } from '../../../../components/';\r\n\r\nexport default class App extends React.Component {\r\n  state = {\r\n    value: 30,\r\n  }\r\n  render () {\r\n    return (\r\n      <Slider\r\n        value={this.state.value}\r\n        min={-100}\r\n        max={100}\r\n        onChange={(value) => {\r\n        console.log('当前值:', value);\r\n        this.setState({value});\r\n      }}/>\r\n    )\r\n  }\r\n}\r\n"